<template>
    <div class="box">
        <!-- <div class="choiceBox">
            <el-select v-model="workYears" placeholder="工作年数" class="choice">
                <el-option v-for="item in workYearsC" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
            </el-select>
            <el-select v-model="eduBackground" placeholder="教育背景" class="choice">
                <el-option v-for="item in eduBackgroundC" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
            </el-select>
            <el-select v-model="age" placeholder="年龄" class="choice">
                <el-option v-for="item in ageC" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
            </el-select>
        </div> -->
        <div class="resume" :style="{ display: hidden }" v-for="i in collectList" :key="i.id">
            <div class="name">{{ i.name }}</div>
            <div class="age"><span>年龄：{{ i.age }}</span> <span>工作年限：{{ i.workingYears }}</span> <span>学历：{{ i.education
            }}</span></div>
            <div class="want"><span>求职期望：{{ i.career }} </span></div>
            <div class="honor"><span>荣誉证书：{{ i.honor }}</span></div>
            <div class="ex">主要经历：{{ i.workExperience }}</div>
            <el-rate v-model="score" disabled text-color="#ff9900" class="score">
            </el-rate>
            <el-button type="primary" class="details" size="mini" @click="goDetail(i.id)">查看详情</el-button>
            <el-button type="info" class="cancel" size="mini" @click="hide(i.id)">取消收藏</el-button>
        </div>
    </div>
</template>

<script>
import { reqCollectResume,reqUnCollect } from '@/api/index'
export default {
    name: 'upload',
    data() {
        return {
            collectList: [],
            hidden: 'block',
            formInline: {
                user: "",
                region: ""
            },
            workYearsC: [{
                value: '1年以下',
                label: '1年以下'
            }, {
                value: '1—3年',
                label: '1—3年'
            }, {
                value: '3—5年',
                label: '3—5年'
            }, {
                value: '5—10年',
                label: '5—10年'
            }, {
                value: '10年以上',
                label: '10年以上'
            }],
            workYears: '',
            eduBackgroundC: [{
                value: '大专',
                label: '大专'
            }, {
                value: '本科',
                label: '本科'
            }, {
                value: '研究生',
                label: '研究生'
            }, {
                value: '大专以下',
                label: '大专以下'
            }],
            eduBackground: '',
            ageC: [{
                value: '20岁以下 ',
                label: '20岁以下 '
            }, {
                value: '20—30岁  ',
                label: '20—30岁 ',
            }, {
                value: '30—40岁   ',
                label: '30—40岁  ',
            },
            {
                value: '40—50岁   ',
                label: '40—50岁  ',
            },
            {
                value: '50岁以上  ',
                label: '50岁以上 ',
            }],
            age: '',
            areaC: [{
                value: '南方地区',
                label: '南方地区'
            }, {
                value: '北方地区',
                label: '北方地区'
            }, {
                value: '东部地区',
                label: '东部地区'
            }, {
                value: '西部地区',
                label: '西部地区'
            }],
            area: '',
            score: '4'
        };
    },
    methods: {
        onSubmit() {
            console.log("submit!");
        }, 
        goDetail(Id) {
            this.$router.push({ name: 'Analyze', params: { id: Id } })
        },
        async hide(id) {
            const res = await reqUnCollect(id)
            if(res.code==1){
                this.$message('取消收藏成功')
                this.getCollectResume()
            }
        },
        async getCollectResume() {
            const res = await reqCollectResume(localStorage.getItem('id'))
            if (res.code == 1) {
                this.collectList = res.data
            }
        }
    },
    mounted() {
        this.getCollectResume()
    }
}
</script>
<style lang="scss" scoped>
::v-deep .el-input__inner {
    border-color: black;
    color: black;

    &::placeholder {
        color: grey;
    }
}

.box {
    width: 83%;

    float: left;
    margin-left: 20px;
    margin-top: 50px;

    // background-color:blue ;
    .choiceBox {
        width: 38%;
        height: 50px;
        display: flex;
        justify-content: space-around;
        align-items: center;
        transform: translate(-6px, -20px);

        .choice {
            scale: 0.9;
        }

        // .choice {
        //     border: 1px black solid;
        //     border-radius: 5%;
        // }
    }

    .resume {
        width: 100%;
        border: 1px solid black;
        height: 150px;
        margin-top: -20px;
        margin-bottom: 15px;
        position: relative;

        .name {
            position: absolute;
            top: 15%;
            left: 8%;
            font-weight: 700;
            font-size: 15px;
        }

        .age {
            position: absolute;
            top: 35%;
            left: 90px;
            width: 250px;

            span {
                margin-left: 10px;
            }
        }

        .want {
            position: absolute;
            top: 55%;
            width: 250px;
            left: 90px;

            span {
                margin-left: 10px;
            }
        }

        .honor {
            position: absolute;
            top: 75%;
            width: 250px;
            left: 90px;


            span {
                margin-left: 10px;
                height: 30px;
                display: block;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }

        .ex {
            width: 40%;
            height: 55%;
            top: 30%;
            left: 40%;
            position: absolute;
            background-color: rgb(246, 249, 252);
            overflow-y: scroll;
        }

        .score {
            position: absolute;
            left: 15%;
            top: 15%;
        }

        .details {
            position: absolute;
            right: 5%;
            top: 30%;
        }

        .cancel {
            position: absolute;
            right: 5%;
            top: 65%;
        }
    }

}
</style>